.productContainer {
  width: 371px;
  height: 444px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 80px;
  padding-left: 15px;
  transition: 0.3s;
  &:hover {
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25);
    .circleIcon {
      display: block;
      width: 52px;
      height: 52px;
      position: absolute;
      margin: 250px 0 -0 289px;
      cursor: pointer;
    }
  }
  .circleIcon {
    width: 52px;
    height: 52px;
    position: absolute;
    margin: -26px 0 0 289px;
    cursor: pointer;
    display: none;
  }
  .projectIcon {
    width: 354px;
    height: 330px;
  }
  .productInfo {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: start;
    width: 354px;
    height: 80px;
    .title {
      font-family: "Raleway";
      font-weight: 300;
      font-size: 17px;
      line-height: 28.8px;
      color: #1d1f22;
      user-select: none;
    }
    .price {
      margin-top: -15px;
      font-family: "Raleway";
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 28.8px;
      text-align: right;
      color: #1d1f22;
      user-select: none;
    }
  }
}

.disabled {
  width: 371px;
  height: 444px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 80px;
  padding-left: 15px;
  transition: 0.3s;
  opacity: 0.4;
  .circleIcon {
    width: 52px;
    height: 52px;
    position: absolute;
    margin: -26px 0 0 289px;
    cursor: pointer;
    display: none;
  }
  .projectIcon {
    width: 354px;
    height: 330px;
    div {
      font-family: "Raleway";
      font-style: normal;
      font-weight: 400;
      font-size: 24px;
      line-height: 160%;
      color: #000000;
      margin-top: -180px;
      margin-left: 90px;
      user-select: none;
    }
  }
  .productInfo {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: start;
    width: 354px;
    height: 80px;
    .title {
      font-family: "Raleway";
      font-weight: 300;
      font-size: 17px;
      line-height: 28.8px;
      color: #1d1f22;
      user-select: none;
    }
    .price {
      margin-top: -15px;
      font-family: "Raleway";
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 28.8px;
      text-align: right;
      color: #1d1f22;
      user-select: none;
    }
  }
}
