.page{
    display:flex;
    align-items:center;
    padding-right: 101px;
  .Wrapper {
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
    margin-right: 29px;
    width: 20px;
    .Text {
      font-family: "Raleway", sans-serif;
      font-weight: 500;
      font-size: 18px;
      line-height: 29px;
      color: #1d1f22;
      user-select: none;
      display: flex;
      align-items: center;
      .Arrow {
        margin-left: 10px;
        transform: rotate(180deg);
        transition: all 0.2s linear;
      }
    }
    .Open {
      margin-left: 10px;
      transform: rotate(0deg);
      transition: all 0.3s linear;
    }
    .List {
      width: 114px;
      box-sizing: border-box;
      list-style: none;
      background-color: white;
      filter: drop-shadow(0px 4px 35px rgba(168, 172, 176, 0.19));
      padding: 12px 0;
      margin: 0;
      cursor: auto;
      position: absolute;
      top: 52px;
      left: -20px;
      .ItemList {
        width: 100%;
        font-family: "Raleway", sans-serif;
        font-size: 18px;
        font-weight: 500;
        box-sizing: border-box;
        padding: 8px 0 8px 20px;
        background-color: white;
        transition: all 0.3s linear;
        cursor: pointer;
        transition: all 0.2s linear;
        &:hover {
          background-color: #eeeeee;
        }
      }
    }
  }
  .Empty{
    cursor: pointer;
    position: relative;
    top:2px;
}
}